@font-face {
  font-style: normal;
  font-family: "Open Sans";
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Open Sans";
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Open Sans";
  font-weight: 300;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  font-size: 14px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: normal;
  color: #283645;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}
.download-app {
  position: relative;
  min-width: 320px;
}
.download-app__wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 320px;
  margin: 0 auto;
  margin-top: 90px;
}
.download-app__header {
  z-index: 2;
  width: 228px;
  margin: 0 auto;
  font-size: 24px;
  color: #ffffff;
  font-weight: lighter;
  text-align: center;
}
.download-app__links {
  z-index: 2;
  width: 320px;
  margin-top: 310px;
  background-color: #1d2631;
}
.download-app__btn {
  display: block;
  width: 210px;
  margin: 30px auto;
  padding: 16px 32px 16px 34px;
  font-size: 18px;
  line-height: 18px;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  border: solid white 2px;
  border-radius: 30px;
  outline: none;
}
.download-app__btn:hover,
.download-app__btn:focus {
  color: #d22856;
  background-color: #ffffff;
}
.download-app__btn:active {
  color: rgba(210, 40, 86, 0.3);
}
.download-app__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 210px;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 0;
  list-style: none;
}
.download-app__item a {
  display: block;
  width: 50px;
  height: 50px;
}
.download-app__item a svg {
  width: 45px;
  height: 45px;
}
.download-app__item a svg:first-of-type {
  height: 60px;
}
.download-app__item:first-of-type {
  height: 60px;
}
.download-app__description {
  width: 240px;
  margin: 0 auto;
  padding-bottom: 30px;
  font-size: 14px;
  line-height: 25px;
  color: #ffffff;
  font-weight: 300;
  text-align: center;
}
.download-app__img {
  position: absolute;
  z-index: 2;
  bottom: 260px;
  width: 260px;
}
.download-app .background-header {
  position: relative;
  height: auto;
  margin: 0 auto;
  margin-top: -70px;
}
@media (min-width: 660px) {
  .download-app {
    position: static;
  }
  .download-app__wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 660px;
    margin: 0 auto;
    margin-top: 75px;
  }
  .download-app__description {
    width: 300px;
    font-size: 18px;
  }
  .download-app .background-header {
    width: 660px;
    min-height: 685px;
  }
  .download-app__img {
    z-index: 1;
    right: 20px;
    bottom: -5px;
    width: 474px;
  }
  .download-app__list {
    margin-bottom: 35px;
  }
  .download-app__links {
    z-index: 2;
    width: 300px;
    margin: 0;
    margin-left: 25px;
    background-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
  }
  .download-app__btn {
    margin-bottom: 45px;
    padding-right: 45px;
    padding-left: 45px;
  }
  .download-app__header {
    width: 200px;
    margin: 0;
    margin-left: 70px;
    font-size: 40px;
    line-height: 50px;
  }
}
@media (min-width: 960px) {
  .download-app .background-header {
    width: auto;
    min-height: 900px;
    margin-top: -140px;
  }
  .download-app__img {
    right: 40px;
    bottom: -47px;
    width: 570px;
  }
  .download-app__wrapper {
    position: relative;
    width: 940px;
    margin: 0 auto;
    margin-top: 200px;
  }
  .download-app__header {
    width: 260px;
    margin-bottom: 55px;
    margin-left: 35px;
    font-size: 50px;
    line-height: 60px;
  }
  .download-app__links {
    width: 305px;
    margin-bottom: 50px;
    margin-left: 15px;
    background-color: rgba(0, 0, 0, 0);
  }
  .download-app__btn {
    width: 245px;
    margin: 0;
    margin-bottom: 65px;
    padding: 15px 20px;
    font-size: 21px;
    border-width: 3px;
  }
}
.app-features {
  position: relative;
}
.app-features__item:nth-of-type(1) .app-features__img,
.app-features__item:nth-of-type(2) .app-features__img {
  display: none;
}
.app-features__header {
  display: flex;
  flex-direction: column;
  width: 215px;
  margin: 0 auto;
  padding-top: 45px;
  padding-bottom: 15px;
  font-size: 24px;
  color: #283645;
  font-weight: 300;
  text-align: center;
}
.app-features__header b {
  margin-top: 27px;
  font-weight: 300;
}
.app-features__list {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 320px;
  margin: 0 auto;
  padding: 0;
  padding-top: 95px;
  list-style: none;
  background-color: #f2f2f2;
  background-image: url("../img/bg-triangle-white.svg");
  background-repeat: no-repeat;
  background-position: 0 -52px;
}
.app-features__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 265px;
  margin: 0 auto;
  margin-bottom: 25px;
  text-align: center;
}
.app-features__item:last-of-type {
  margin-bottom: 0;
}
.app-features__item:last-of-type .app-features__paragraph {
  margin-bottom: 40px;
}
.app-features__heading {
  margin: 0;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 24px;
  color: #000000;
  font-weight: 700;
  order: 1;
}
.app-features__img {
  height: 278px;
  order: 3;
}
.app-features__paragraph {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  order: 2;
}
.app-features__paragraph br {
  display: none;
}
@media (min-width: 660px) {
  .app-features__wrapper {
    width: 660px;
    min-height: 730px;
    margin: 0 auto;
  }
  .app-features__header {
    width: 593px;
    padding-top: 45px;
    padding-bottom: 30px;
    font-size: 40px;
    line-height: 50px;
  }
  .app-features__header b {
    width: 350px;
    margin: 0 auto;
  }
  .app-features__img {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 268px;
    height: 481px;
  }
  .app-features__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 660px;
    margin: auto;
    margin-bottom: 40px;
    padding-top: 85px;
    padding-bottom: 40px;
    background-position: 0 -30px;
  }
  .app-features__paragraph {
    width: 310px;
  }
  .app-features__paragraph br {
    display: block;
  }
  .app-features__item {
    display: block;
    width: 330px;
    margin: 0;
    margin-bottom: 35px;
    margin-left: auto;
    text-align: left;
  }
  .app-features__item:last-of-type .app-features__paragraph {
    margin-bottom: 0;
  }
}
@media (min-width: 960px) {
  .app-features__wrapper {
    width: auto;
    background-color: #f2f2f2;
    background-image: url("../img/bg-triangle-white.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
  .app-features::before {
    display: none;
  }
  .app-features__img {
    top: 0;
    width: 292px;
    height: 302px;
    margin-bottom: 55px;
  }
  .app-features__item:nth-of-type(3) .app-features__img {
    left: -10px;
  }
  .app-features__img:nth-child(1),
  .app-features__img:nth-child(2) {
    display: block;
  }
  .app-features__header {
    width: 760px;
    padding-top: 50px;
    padding-bottom: 80px;
    font-size: 50px;
    line-height: 60px;
  }
  .app-features b {
    width: 440px;
    margin: 0 auto;
  }
  .app-features__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 890px;
    margin-bottom: 30px;
    padding-top: 0;
    background-color: rgba(0, 0, 0, 0);
    background-image: none;
  }
  .app-features__list::before {
    display: none;
  }
  .app-features__item {
    position: relative;
    width: 230px;
    margin: 0;
  }
  .app-features__heading {
    width: 230px;
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
  }
  .app-features__paragraph {
    width: 240px;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }
  .app-features__paragraph br {
    display: none;
  }
}
.reviews {
  position: relative;
  width: 320px;
  margin: 0 auto;
  padding-top: 45px;
  background-color: #ffffff;
}
.reviews__list {
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
.reviews__item {
  text-align: center;
}
.reviews__author-name {
  margin: 0;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 30px;
  color: #000000;
  font-weight: 700;
}
.reviews__author-info {
  display: none;
}
.reviews__review-text {
  margin: 0;
  margin-bottom: 25px;
  padding: 0 20px;
  font-size: 14px;
  line-height: 30px;
  color: #283645;
}
.reviews-buttons {
  width: 70px;
  margin: 0 auto;
}
.reviews-buttons__item {
  width: 12px;
  height: 12px;
  margin-right: 8px;
  padding: 0;
  cursor: pointer;
  border: solid 3px #ffffff;
  border-radius: 50%;
  background-color: #dadddf;
}
.reviews-buttons__item:last-of-type {
  margin-right: 0;
}
.reviews-buttons__item--active {
  width: 12px;
  height: 12px;
  border: solid 3px #283645;
  background-color: #ffffff;
}
.reviews-buttons__item-last,
.reviews-buttons__item-next {
  display: none;
}
@media (min-width: 660px) {
  .reviews {
    width: 660px;
    padding-top: 5px;
  }
  .reviews__list {
    width: 620px;
  }
  .reviews__review-text {
    width: 620px;
    margin-bottom: 30px;
    padding: 0;
    font-size: 18px;
    order: 0;
  }
  .reviews__author-name {
    width: 620px;
    margin-bottom: 5px;
    font-size: 20px;
    order: 1;
  }
  .reviews__author-info {
    display: block;
    width: 620px;
    margin-bottom: 25px;
    order: 1;
  }
  .reviews__item {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 960px) {
  .reviews {
    width: 940px;
    padding-top: 180px;
    padding-bottom: 10px;
    background: url("../img/icon-quotes.svg") no-repeat center 50px;
  }
  .reviews__list {
    width: 690px;
  }
  .reviews__author-name {
    width: 690px;
    font-size: 24px;
  }
  .reviews__author-info {
    width: 690px;
    font-size: 18px;
  }
  .reviews__review-text {
    width: 690px;
  }
  .reviews-buttons {
    position: absolute;
    top: 200px;
    width: 940px;
    height: 40px;
    margin: 0;
    cursor: pointer;
  }
  .reviews-buttons__item {
    display: none;
  }
  .reviews-buttons__item-last {
    position: absolute;
    left: 0;
    display: block;
    width: 40px;
    height: 45px;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    background-color: rgba(0, 0, 0, 0);
  }
  .reviews-buttons__item-last:hover::before,
  .reviews-buttons__item-last:hover::after {
    opacity: 1;
  }
  .reviews-buttons__item-last:active::before,
  .reviews-buttons__item-last:active::after {
    opacity: 0.1;
  }
  .reviews-buttons__item-last::before {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 30px;
    height: 2px;
    content: "";
    transform: rotate(45deg);
    opacity: 0.3;
    background-color: #7e868f;
  }
  .reviews-buttons__item-last::after {
    position: absolute;
    top: 13px;
    left: 0;
    width: 30px;
    height: 2px;
    content: "";
    transform: rotate(-45deg);
    opacity: 0.3;
    background-color: #7e868f;
  }
  .reviews-buttons__item-next {
    position: absolute;
    right: 0;
    display: block;
    width: 40px;
    height: 45px;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    background-color: rgba(0, 0, 0, 0);
  }
  .reviews-buttons__item-next:hover::before,
  .reviews-buttons__item-next:focus::before,
  .reviews-buttons__item-next:hover::after,
  .reviews-buttons__item-next:focus::after {
    opacity: 1;
  }
  .reviews-buttons__item-next:active::before,
  .reviews-buttons__item-next:active::after {
    opacity: 0.1;
  }
  .reviews-buttons__item-next::before {
    position: absolute;
    right: 0;
    bottom: 10px;
    width: 30px;
    height: 2px;
    content: "";
    transform: rotate(-45deg);
    opacity: 0.3;
    background-color: #7e868f;
  }
  .reviews-buttons__item-next::after {
    position: absolute;
    top: 13px;
    right: 0;
    width: 30px;
    height: 2px;
    content: "";
    transform: rotate(45deg);
    opacity: 0.3;
    background-color: #7e868f;
  }
}
.main-header {
  position: relative;
  z-index: 2;
}
.main-header__wrapper {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
.main-header__logo {
  display: flex;
  align-items: center;
  height: 69px;
  padding-left: 20px;
  text-align: left;
  outline: none;
  background-color: #283645;
  fill: #ffffff;
}
.main-header__logo[href]:hover .main-header__logo-img,
.main-header__logo[href]:focus .main-header__logo-img {
  fill: #d22856;
}
.main-header__logo[href]:active .main-header__logo-img {
  opacity: 0.3;
  fill: #ffffff;
}
.main-header__logo-tablet,
.main-header__logo-desktop {
  display: none;
}
.main-header__logo-mobile {
  display: block;
}
@media (max-width: 939px) {
  .main-header--opened {
    z-index: 2;
  }
  .main-header--closed {
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 660px) {
  .main-header__logo {
    width: auto;
    height: 120px;
    padding-left: 20px;
  }
  .main-header__logo-tablet,
  .main-header__logo-mobile {
    display: none;
  }
  .main-header__logo-tablet {
    display: block;
  }
  .main-header__wrapper {
    width: 660px;
  }
}
@media (min-width: 960px) {
  .main-header__logo {
    height: 110px;
    margin-left: 0;
    padding-left: 0;
    background-color: rgba(0, 0, 0, 0);
  }
  .main-header {
    z-index: 2;
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-header__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 940px;
    margin: 0 auto;
  }
  .main-header__logo-tablet,
  .main-header__logo-mobile {
    display: none;
  }
  .main-header__logo-desktop {
    display: block;
  }
}
.main-nav {
  width: 320px;
  margin: 0 auto;
  background-color: #283645;
}
.main-nav__item {
  padding: 15px 0;
  border-top: solid 1px #485461;
  border-bottom: solid 1px #000000;
}
.main-nav__item:first-child {
  box-shadow: 0 -1px 0 #000000;
}
.main-nav__item a {
  font-size: 18px;
  line-height: 32px;
  color: #ffffff;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  outline: none;
}
.main-nav__item a[href]:hover,
.main-nav__item a[href]:focus {
  color: #d22856;
}
.main-nav__item a[href]:active {
  opacity: 0.3;
}
.main-nav__list {
  width: 320px;
  margin: 0 auto;
  padding: 0;
  text-align: center;
  list-style: none;
}
.main-nav__toggle {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  display: block;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle {
  top: 20px;
  right: 20px;
  width: 50px;
  height: 24px;
  outline: none;
  background: url("../img/icon-menu-burger-white.svg") no-repeat 0 0;
}
.main-nav--closed .main-nav__toggle:hover {
  background: url("../img/icon-menu-burger-pink.svg") no-repeat 0 0;
}
.main-nav--closed .main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle {
  top: 20px;
  right: 20px;
  display: none;
  width: 24px;
  height: 24px;
  outline: none;
  background: url("../img/icon-menu-cross-white.svg") no-repeat 0 0;
}
.main-nav--opened .main-nav__toggle:hover {
  background: url("../img/icon-menu-cross-pink.svg") no-repeat 0 0;
}
.main-nav--opened .main-nav__toggle:active {
  opacity: 0.3;
}
@media (max-width: 939px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (min-width: 660px) {
  .main-nav {
    width: auto;
    margin: 0;
  }
  .main-nav__list {
    width: 660px;
  }
}
@media (min-width: 960px) {
  .main-nav {
    position: relative;
    display: flex;
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0);
  }
  .main-nav__item {
    border: none;
  }
  .main-nav__item:first-child {
    box-shadow: none;
  }
  .main-nav__item a {
    font-weight: 400;
    text-transform: none;
    border: none;
  }
  .main-nav__item .main-nav--active {
    padding-bottom: 8px;
    border-bottom: solid white 2px;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 620px;
    padding-right: 10px;
  }
  .main-nav__toggle {
    display: none;
  }
}
.main-footer {
  padding-bottom: 40px;
  background-color: #ffffff;
  box-sizing: border-box;
}
@media (max-width: 660px) {
  .main-footer--index {
    display: none;
  }
}
.main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 280px;
  margin: 0 auto;
}
.main-footer__logo {
  display: block;
  width: 150px;
  margin-bottom: 35px;
  outline: 0;
  fill: #283645;
}
.main-footer__logo[href]:hover .main-footer__logo-img,
.main-footer__logo[href]:focus .main-footer__logo-img {
  fill: #d22856;
}
.main-footer__logo[href]:active .main-footer__logo-img {
  opacity: 0.3;
  fill: #283645;
}
.main-footer__logo-mobile {
  display: block;
}
.main-footer__logo-desktop {
  display: none;
}
.main-footer__logo img {
  width: 150px;
}
.socials-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 175px;
  margin: 0;
  margin-bottom: 35px;
  padding: 0;
  list-style: none;
}
.developed-by {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 135px;
  margin: 0;
  outline: none;
}
.developed-by-right {
  display: block;
  width: 40px;
  height: 45px;
  outline: none;
}
.developed-by-right svg {
  width: 40px;
  height: 45px;
  fill: #d22856;
}
.developed-by-right:hover svg,
.developed-by-right:focus svg {
  fill: #283645;
}
.developed-by-right:active svg {
  fill: rgba(40, 54, 69, 0.3);
}
.developed-by b {
  font-weight: 400;
}
